import axios from "axios";
import { APP_URL } from "../config/app.config";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";

// action url
const url = APP_URL;

export interface SendPermutation__Interface {
  message: string;
  permutation_code: string;
}

export interface SendPermutation__Interface_Res {
  message: string;
  permutation_code: string;
}

export interface SendMutationData {
  role_id: string;
  user_one: string;
  user_two: string;
  position_code: string;
  qualification_id: string;
  school_one: string;
  school_two: string;
  academic_year_id: string;
}

export const FC_CreatePermutation = async (
  data: SendMutationData,
  callBack: (
    status: boolean,
    data: SendPermutation__Interface_Res | null,
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.post<SendPermutation__Interface_Res>(
      `${url}/permutation/create`,
      data
    );

    callBack(true, res.data, "");
  } catch (error) {
    callBack(
      false,
      null,
      errorToText(error, () => {})
    );
  }
};

export interface PermutationUser__Interface {
  user_id: string | null;
  first_name: string | null;
  middle_name: string | null;
  last_name: string | null;
  full_name: string | null;
  staff_code: string | null;
  sex: string | null;
  dob: string | null;
  marital_status: string | null;
  nid: string | null;
  email: string | null;
  phone_numbers: string | null;
}

export interface PermutationSchool__Interface {
  school_name: string;
  school_code: string;
}

export interface PermutationItem__Interface {
  permutation_id: string | null;
  role_id: string | null;
  user_one: string | null;
  user_two: string | null;

  permutation_code: string | null;
  position_code: string | null;
  qualification_id: string | null;

  school_one: string | null;
  school_two: string | null;
  academic_year_id: string | null;
  user_one_date: string | null;
  user_two_date: string | null;
  permutation_status: string | null;
  reb_user_id: string | null;
  reb_status: string | null;
  reb_date: string | null;
  role: string | null;
  position_name: string | null;
  qualification_name: string | null;
  usersInfo: PermutationUser__Interface[];
  schools: PermutationSchool__Interface[];
}

export const FC_SearchPermutationByCode = async (
  code: string,
  callBack: (
    status: boolean,
    data: PermutationItem__Interface[],
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<PermutationItem__Interface[]>(
      `${url}/permutation/usercode/${code}`
    );

    console.log({ res });

    callBack(true, res.data, "");
  } catch (error) {
    callBack(
      false,
      [],
      errorToText(error, () => {})
    );
  }
};

export const FC_GetMyPermutation = async (
  user_id: string,
  callBack: (
    status: boolean,
    data: PermutationItem__Interface[],
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<PermutationItem__Interface[]>(
      `${url}/permutation/user/${user_id}`
    );

    callBack(true, res.data, "");
  } catch (error) {
    callBack(
      false,
      [],
      errorToText(error, () => {})
    );
  }
};

export interface UserApprovePermutation {
  permutation_id: string;
  permutation_status: "Accepted" | "Rejected";
}
export const FC_UserApprovePermutation = async (
  dataSend: UserApprovePermutation,
  callBack: (
    status: boolean,
    data: { msg: string } | null,
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.post<{ msg: string }>(
      `${url}/permutation/userdecision`,
      dataSend
    );

    callBack(true, res.data, "");
  } catch (error) {
    callBack(
      false,
      null,
      errorToText(error, () => {})
    );
  }
};
