export type FT_GetPosyLimit = (
  academic_id: string,
  callBack: (
    status: boolean,
    post_limits: GetPostLimit | null,
    msg: string
  ) => void
) => void;
export const FC_GetPosyLimit = (
  academic_id: string,
  callBack: (
    status: boolean,
    post_limits: GetPostLimit | null,
    msg: string
  ) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<GetPostLimit>(
        `${url}/minicofinlimit/academic/${academic_id}`
      );
      // * save data
      callBack(true, res.data, "");
    } catch (error) {
      callBack(false, null, errorToText(error));
    }
  };
};

export type FT_SavePostLimit = (
  data: PostLimit[],
  callBack: (status: boolean, msg: string) => void
) => void;
export const FC_SavePostLimit = (
  data: PostLimit[],
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.post<{ message: string }>(
        `${url}/minicofinlimit/create`,
        data
      );
      // * save data
      callBack(true, res.data.message);
    } catch (error) {
      callBack(false, errorToText(error));
    }
  };
};

