export interface GetFreeVacanciesByDistrictAndUser_Interface {
  sector_name: string;
  school_code: string;
  school_name: string;
  vacant_posts: number;
}
export const FC_GetFreeVacanciesByDistrictAndUser = async (
  district_id: string,
  user_id: string,
  callBack: (
    status: boolean,
    data: GetFreeVacanciesByDistrictAndUser_Interface[],
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.post<GetFreeVacanciesByDistrictAndUser_Interface[]>(
      `${url}/teachertransfer/getschoolsperdistrict/${district_id}`,
      {
        user_id: user_id,
        // user_id: "006aaa56-6eb1-464f-8039-22538bc2ade7",
      }
    );

    callBack(
      true,
      res.data.filter(
        (itm: GetFreeVacanciesByDistrictAndUser_Interface) =>
          +itm.vacant_posts >= 1
      ),
      ""
    );
  } catch (error) {
    callBack(
      false,
      [],
      errorToText(error, () => {})
    );
  }
};

